.orderPage {
  padding: 2rem !important;
  display: grid;
  grid-template-columns: 2fr 1fr;
}
.confirmCartItems > p,
.orderSummary > p,
.confirmShippingArea > p,
.confirmStatusForm>p,
.statusForm>p {
  font: 400 1.8vmax "Roboto";
  text-decoration: underline;
}

.confirmShippingArea > div > div {
  display: flex;
  margin: 1vmax 0;
}
.confirmShippingArea > div > div > p {
  font: 400 1.2vmax "Roboto";
  color: black;
}

.confirmShippingArea > div >div> span {
  font: 100 1.2vmax "Roboto";
  margin: 0 1vmax;
  color: #575757;
}

.orderDetailsCartItemsContainer > div {
    display: flex;
    font: 400 1vmax "Roboto";
    align-items: center;
    justify-content: space-between;
    margin: 2vmax 0;
  }
  
  .orderDetailsCartItemsContainer > div > a > img {
    width: 5vmax;
    margin-right: 3vmax;
  }
  
  .orderDetailsCartItemsContainer > div > a {
    color: #575757;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font: 100 1.5vmax "Roboto";
  }
  
  .orderDetailsCartItemsContainer > div > span {
    font: 100 1.5vmax "Roboto";
    color: #5e5e5e;
  }
.orderSummary {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.orderSummary > div > div {
  margin: 1vmax;
}

.orderSummary > div > div:first-child {
  margin-left: 0;
}
.orderSummary > div > div > p {
  display: inline;
  margin-right: 0.5vmax;
  font: 400 1.3vmax "Roboto";
}

.orderSummary > div > div > span {
  /* text-decoration: underline; */
  font: 400 1.3vmax "Roboto";
}

.confirmStatusForm{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.statusForm{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 2rem;
}
.statusForm>div{
  margin: 1rem 0;
  display: flex;
  align-items: center;
}
.statusForm>div:last-child{
  margin: 1rem 0;
  display: flex;
  align-items: center;
  justify-content: center;  
}
.statusForm>div>select
 {
    margin:0;
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;

}

.statusForm>div>select>option{
    font: 400 1.2vmax "Roboto";
}

.statusForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.687);
}

.updateStatusBtn{
  margin: 0!important;
}
@media screen and (max-width: 600px) {
  .orderPage {
    padding: 3vmax;
    padding-top: 0;
    grid-template-columns: 1fr;
  }
  .confirmCartItems > p,
  .orderSummary > p,
  .confirmShippingArea > p {
    font: 400 3vmax "Roboto";
  }
  .confirmShippingArea {
    border: none;
  }
  .orderSummary > div {
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: column;
  }
  .orderSummary > div > div {
    margin: 0.5vmax;
  }

  .confirmShippingArea > div > p {
    font: 400 2.5vmax "Roboto";
    color: black;
  }
  .confirmShippingArea > div.address > span {
    width: 100%;
  }
  .confirmShippingArea > div > span {
    font: 400 2.5vmax "Roboto";
  }
  .confirmShippingArea {
    padding-left: 0;
  }

  .confirmCartItemsContainer > div > a {
    font: 100 3vmax "Roboto";
  }

  .confirmCartItems {
    margin: 0;
  }
  /* .orderSummary,
    .confirmShippingArea {
      margin-top: 5vmax;
    } */
  .confirmCartItemsContainer > div > a > img {
    width: 10vmax;
    margin-right: 2vmax;
  }

  .confirmCartItemsContainer > div > span {
    font: 100 2vmax "Roboto";
    color: #5e5e5e;
  }

  .orderSummary > div > div > p {
    font: 400 2.5vmax "Roboto";
  }

  .orderSummary > div > div > span {
    font: 400 2.5vmax "Roboto";
  }

  .proceedToPay {
    animation-name: unset;
  }

  .orderSummary > div > div:last-child > button {
    padding: 2vmax 5vmax;
    font: 400 2.5vmax "Roboto";
    margin: 0;
  }
}
