.sideBar {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width:230px;

}

.sideBar > a:first-child {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sideBar > a > img {
  width: 80px;
  transition: all 0.5s;
  margin-bottom: 2vmax;
}

.sideBar > a > img:hover {
  filter: drop-shadow(0 0 10px tomato);
}

.sideBar a,
.sideBar>ul {
  text-decoration: none;
  transition: all 0.2s;
  color: rgba(0, 0, 0, 0.493);
  padding: 1rem;
  font: 200 1rem "Roboto";
  display: flex;
  align-items: center;
  margin-inline: auto;
}

.sideBar a:hover,
.sideBar>ul:hover {
  color: tomato;
}

.sideBar a > p {
  margin-left: 0.5rem;
}

/* .sideBar > ul > li > div >div:last-child{
  display: none;
} */



.MuiTypography-root {
  background-color: #fff !important;
}

@media screen and (max-width: 600px) {
  .sideBar {
    position: relative;
    height: 100vh;
    width: 100vw;
    margin: auto;
  }
}
